import React, { Component } from 'react';
import Select from 'react-select';
import * as CustomItem from './custom_list_items';

class SelectItem extends Component {
	render() {
		const {id, title, options, selectedIndex = 0} = this.props;

		return (
			<div className="form-item">
				<label htmlFor={id}>{title}</label>
				<Select
					defaultValue={options[selectedIndex]}
					options={options}
				/>
			</div>
		)
	}
}

class CheckboxItem extends Component {
	render() {
		const {id, checked, title} = this.props;

		return (
			<div className="list-item">
				<div className="custom-checkbox">
					<input type="checkbox" name="questions" id={id} defaultChecked={checked} />
					<label htmlFor={id} >{title}</label>
				</div>
				<div className="help-tip">{this.props.help_text}</div>
			</div>
		)
	}
}

class RadioItems extends Component {
	render() {
		const {id, title, options, value} = this.props;

		return (
			<div>
				<div>{title}</div>

				{options.map((option, index) => (
					<div className="list-item" key={`question_${id}_${index}`}>
						<div className="custom-radio _approach">
							<input
								type="radio"
								name={`questions_${id}`}
								id={`question_${id}_${index}`}
								value={option.value}
								defaultChecked={(
									(!value && index === 0) || option.value === value
								)}
							/>
							<label htmlFor={`question_${id}_${index}`} >{option.title}</label>
							<div className="description">
								{option.description}
							</div>
							<div className="link-tip">
								<a href="" className="link">What's this?</a>
							</div>
						</div>
					</div>
				))}
			</div>

		)
	}
}

class ListItem extends Component {
	render() {
		const {type, options, component} = this.props;

		return (
			component ? (
				CustomItem.get(component, this.props)
			) : type === 'radio' ? (
				<RadioItems {...this.props}/>
			) : options ? (
				<SelectItem {...this.props} />
			) : (
				<CheckboxItem {...this.props} />
			)
		)
	}
}

export default ListItem;


