import React, { Component } from 'react';
import LanguageSelect from "./language_select";

class TopBar extends Component {
	state = {
		isActive: true
	};

	ToggleClass = () => {
		this.setState({ isActive: !this.state.isActive })
	};

	render() {
		return(
			<div className={`top-bar ${(this.state.isActive) ? 'menu-closed' : 'menu-open'}`}>
				<a href="#" className="menu-toggle" onClick={this.ToggleClass} >
					<div className="line" />
				</a>
				<div className="logo" />
				<div className="main-nav">
					<div className="menu">
						<a href="#" className="menu-link">About Us</a>
						<a href="#" className="menu-link">Services</a>
						<a href="#" className="menu-link">Projects</a>
						<a href="#" className="menu-link">FAQ</a>
						<a href="#" className="menu-link">Contact Us</a>
					</div>
					<a href="http://delta.at" target="_blank" className="button">Back to Delta.at</a>
					<div className="language">
						<LanguageSelect />
					</div>
				</div>
			</div>
		)
	}
}

export default TopBar;


