import React, { Component } from 'react';

import { Route } from "react-router-dom";

import ConfigMenu from "../components/configurator/config_menu";
import Views from "./views";
import ViewsJson from "../components/configurator/views.json";
import ViewsOptions from "../components/configurator/view_options.json";
import EstimatedCost from "./estimated_cost";

const Illustration = ({step}) => (
	<div className={`illustration step-${step + 1}`} />
);

class Content extends Component {
	constructor(props) {
		super(props);
		this.onRouteChanged = this.onRouteChanged.bind(this);
	}

	state = {
		step: 0
	};

	onRouteChanged(id) {
		this.setState({step: id});
	}

	steps = ViewsJson.map(({options = [], ...rest}, index) => ({
		...rest,
		index,
		options: options.map((view_id) => {
			const isIdMatch = (view_item) => (
				view_item.id === view_id
			);

			return ViewsOptions.find(isIdMatch);
		})
	}));

	routes = this.steps.map((view_type, index) => {
		const render = (props) => (
			<Views {...props}
				   {...view_type}
				   items={view_type.options}
				   next_step_data={ViewsJson[index + 1]}
				   prev_step_data={ViewsJson[index - 1]}
				   onRouteChanged={this.onRouteChanged}
			/>
		);

		return(
			<Route path={`/${view_type.path}`}
				   render={render}
				   key={`${view_type.id}_route`}
			/>
		)
	});

	render() {
		const {step} = this.state;
		const {is_contact_data_exist, submitContactData} = this.props;

		return(
			<div className="page-content">
				<div className="navigation">
					<ConfigMenu current_step={step} />
				</div>
				<div className="content">
					{step !== this.props.last_step &&
						<Illustration step={step} />
					}

					{this.routes}

					<Route exact path="/estimated-cost" component={() => (
						<EstimatedCost submitContactData={submitContactData}
									   is_contact_data_exist={is_contact_data_exist}
									   steps={this.steps}
						/>
					)}/>
				</div>
			</div>
		);
	}
}

Content.defaultProps = {
	last_step: 5
};

export default Content;


