import React, { Component } from 'react';

import Graphic from "../components/configurator/graphic";
import ListItem from "../components/configurator/list_item";
import ConfigMenu from "../components/configurator/config_menu";
import ConfigButtons from "../components/configurator/config_buttons";

class Views extends Component {
	// get graphic() {
	//
	// 	const graphic_array = [
	// 		{ src: "sample" },
	// 		{ src: "sample_2" },
	// 		{ src: "sample" },
	// 		{ src: "sample_2" },
	// 		{ src: "sample" }
	// 	];
	//
	// 	return this.props.graphic_array.map(graphic_src => (
	// 		<Graphic key={`graphic_${graphic_src.src}`} {...graphic_src}/>
	// 	));
	// }

	componentDidMount() {
		this.props.onRouteChanged(this.props.id);
	}

	get buttons() {
		console.log(this.props.id);
		return this.props.items.map(view => (
			<ListItem key={`view_option_${view.id}`} {...view}/>
		))
	}

	get options() {
		return this.props.items.map(view => (
			<ListItem key={`view_option_${view.id}`} {...view}/>
		))
	}

	render() {
		const {id, title, items, prev_step_data, next_step_data, history} = this.props;

		if (items.length === 0) {
			return null;
		}

		return(
			<div className="choice">
				<div className="choice-list">
					<div className="choice-title">
						{id + 1}. {title}
					</div>
					<div className="choice-subtitle">
						{this.props.subtitle}
					</div>
					<div className="list">
						{this.options}
					</div>
				</div>
				<ConfigButtons
					id={id}
					prev_step_data={prev_step_data}
					next_step_data={next_step_data}
					onClick={history.push}
				/>
			</div>
		)
	}
}

Views.defaultProps = {
	items: []
};

export default Views;


