import React, { Component } from 'react';
import { renderToStaticMarkup } from "react-dom/server";
import { Translate, withLocalize } from "react-localize-redux";
import TranslateHomepage from "./localization.json";
import {
	NavLink
} from "react-router-dom";

import {
	Accordion,
	AccordionItem,
	AccordionItemTitle,
	AccordionItemBody,
} from 'react-accessible-accordion';
import 'react-accessible-accordion/dist/fancy-example.css';
import Footer from "../components/footer";

class Main extends Component {

	constructor(props) {
		super(props);

		this.props.initialize({
			languages: [
				{ name: "English", code: "en" },
				{ name: "", code: "ua" }
			],
			translation: TranslateHomepage,
			options: {
				renderToStaticMarkup,
				renderInnerHtml: true,
				defaultLanguage: "en"
			}
		});
	}

	render() {
		return (
			<div className="homepage">
				{/*<Translate id="homepage.title" />*/}
				<div className="header">
					<div className="container">
						<h1 className="title">DELTA Service <br />Configurator</h1>
						<h3 className="subtitle">Get the most accurate evaluation of your project in a few minutes, free of charge.</h3>
						<NavLink replace to={`/building-type`} className="button">Get started</NavLink>
					</div>
				</div>
				<div className="faq">
					<div className="container">
						<div className="faq-title">
							FAQ
						</div>
						<div className="faq-subtitle">
							Find quick answers.
						</div>
						<Accordion>
							<AccordionItem>
								<AccordionItemTitle>
									<h3>How long will it take to estimate cost of my project</h3>
								</AccordionItemTitle>
								<AccordionItemBody>
									<p>Body content</p>
								</AccordionItemBody>
							</AccordionItem>
							<AccordionItem>
								<AccordionItemTitle>
									<h3>Can I save or share final estimation of my project?</h3>
								</AccordionItemTitle>
								<AccordionItemBody>
									<p>Body content</p>
								</AccordionItemBody>
							</AccordionItem>
							<AccordionItem>
								<AccordionItemTitle>
									<h3>How can I contact with DELTA manager?</h3>
								</AccordionItemTitle>
								<AccordionItemBody>
									<p>Body content</p>
								</AccordionItemBody>
							</AccordionItem>
							<AccordionItem>
								<AccordionItemTitle>
									<h3>What if I will have questions through this process?</h3>
								</AccordionItemTitle>
								<AccordionItemBody>
									<p>Body content</p>
								</AccordionItemBody>
							</AccordionItem>
						</Accordion>
					</div>
				</div>
				<Footer />
			</div>
		);
	}
}

export default withLocalize(Main);


